.App{
	background-color: rgb(252, 207, 207,0.8);
	border-radius: 100px;
	position: relative;
	width: 600px;
	max-height: 400px;
	margin:40px auto
	
}

h1{
  color: #FFF;
  font-family: "Kanit";
  font-size: 40px;
  line-height: 1em;
  margin: 0;
  position: relative;
  transform: translateY(70px);
  width: 100%;
  text-shadow: 0 1px 0 #e4adad, 0 2px 0 #e1a6a6, 0 3px 0 #df9e9e, 0 4px 0 #dc9696, 0 5px 0 #da8f8f, 0 6px 0 #d78787, 0 7px 0 #d58080, 0 8px 0 #d27878, 0 0 5px rgba(237, 154, 154, 0.05), 0 -1px 3px rgba(237, 154, 154, 0.2), 0 9px 9px rgba(237, 154, 154, 0.5), 0 12px 12px rgba(237, 154, 154, 0.5), 0 15px 15px rgba(237, 154, 154, 0.5);
}

#form-todo{

	transform: translate(0px, 150px);
}

#form-todo input{
	font-size: larger;
	text-align: center;
	height: 50px;
	width: 400px;
	border-style: double;
	border-color: #ecf3f3;
	border-radius: 20px ;
	box-shadow: 3px 3px 10px hsl(221, 25%, 71%);
	outline: #ecf3f3;
	cursor: text;
	
}



