.list{
    color: rgb(255, 255, 255);
    text-align: center ;
    width: 300px;
    margin: 20px auto;
    outline-color: azure;
    height: 50px;
    background-color: rgba(233, 163, 142, 0.8);
    border-radius: 30px;
    box-shadow: 3px 3px 50px rgb(209, 146, 146);
    transform: translateY(170px);

}

.list:nth-child(odd) {
    color: rgb(255, 255, 255);
    text-align: center ;
    width: 350px;
    margin: 20px auto;
    outline-color: azure;
    height: 50px;
    background-color: rgba(149, 202, 218, 0.8);
    border-radius: 30px;
    box-shadow: 1px 1px 50px rgb(91, 100, 112);
    transform: translateY(170px);
}
.list p{
    padding: 10px;
    position: relative;
    transition: ease-in-out;
}
.list p input{

    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 25px;
}
.list p input:focus{
    outline: none;
}
.list p span{
    position: absolute;
    right: 10px;
}
.faicons{
    transform: translate(5px,-36px);
    font-size: 40px;
    cursor: pointer;
    color: rgb(255, 254, 253);
    
}
.itemsAnim-enter{
    opacity: 0.01;
}
.itemsAnim-enter .itemsAnim-enter-active{
    opacity: 1;
    transition: opacity 300ms ease-in
}
.itemsAnim-leave{
    opacity: 1;
}
.itemsAnim-leave .itemsAnim-leave-active{
    opacity: 0.01;
    transition: opacity 300ms ease-in
}
body {
